/***
The vendors/ folder (may) contain all the CSS files from external libraries 
and frameworks; e.g. Normalize, Bootstrap, Foundation, and so on.
***/
/***
The base/ folder holds what we might call the boilerplate code for the project. 
In there, you might find the reset file, some typographic rules, and probably a 
stylesheet defining some standard styles for commonly used HTML elements.
***/
body {
  background-color: #f4f4f6;
  color: rgba(0, 0, 0, 0.695);
  font-weight: normal;
  font-style: normal;
  line-height: 1.68;
  letter-spacing: -.003em;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}

/* Never like UIkit's red <em> color */
em {
  color: inherit;
  letter-spacing: -0.015em;
}

@media screen and (min-width: 768px) {
  /* to accomodate the centering of main.uk-container */
  body {
    padding-left: 350px;
  }
}

a, a:link, a:visited {
  color: var(--color-primary);
  text-decoration: underline;
  word-break: break-word;
}

a.token.url-link {
  color: #98c379;
  text-decoration: underline;
}

a:hover, a:link:hover, a:visited:hover {
  text-decoration: none;
}

/* TYPOGRAPHY */
h1 {
  border-top: 4px solid #ddd;
  padding-top: 3.5rem;
  margin-top: 4rem;
}

main > article:first-of-type > h1 {
  border: none;
  padding-top: 0;
  margin-top: 0;
}

main blockquote {
  border-left: 4px solid #ddd;
  color: #777;
  padding: 0 1rem;
}

/***
The layout/ folder contains everything that takes part in laying out the site 
or application. This folder could have stylesheets for the main parts of the 
site (header, footer, navigation, sidebar…), the grid system or even CSS 
styles for all the forms.
***/
@media screen and (min-width: 768px) {
  /* main.uk-container */
  .uk-container {
    width: auto;
    max-width: 740px;
    margin: 0 auto;
  }
}

.uk-navbar,
.uk-navbar-container:not(.uk-navbar-transparent),
.uk-navbar-dropdown {
  color: #fff;
  background: var(--color-secondary);
}

.uk-navbar-dropdown-nav .uk-nav-header {
  color: #fff;
}

.uk-navbar-nav > li.uk-active > a {
  color: var(--color-accent) !important;
}

.uk-navbar-nav > li > a:focus,
.uk-navbar-nav > li > a:hover {
  color: rgba(255, 255, 255, 0.7);
}

.uk-navbar-dropdown,
.uk-navbar-dropdown-nav {
  min-width: auto;
}

.uk-offcanvas-bar {
  background: var(--color-secondary);
}

.uk-offcanvas-bar .uk-nav-default .uk-active,
.uk-offcanvas-bar .uk-nav-default .uk-nav-sub .uk-active {
  color: var(--color-accent);
}

@media screen and (min-width: 768px) {
  /* force-display sidebar on 768+ */
  .uk-offcanvas {
    display: block;
  }
  .uk-offcanvas-bar {
    transform: translateX(0);
  }
  .uk-offcanvas-close {
    display: none;
  }
}

/***
For smaller components, there is the components/ folder. While layout/ is macro 
(defining the global wireframe), components/ is more focused on widgets. It 
contains all kind of specific modules like a slider, a loader, a widget, and 
basically anything along those lines. There are usually a lot of files in 
components/ since the whole site should be mostly composed of tiny modules.
***/
:not(pre) > code,
:not(pre) > kbd,
:not(pre) > samp {
  color: var(--color-secondary);
  background: var(--color-accent);
}

pre {
  border: 0;
  border-radius: 0;
  padding: 0;
}

/* CUSTOM PRISM KEYWORDS */
.language-xml .token.cdata {
  color: var(--color-accent);
}

/* PRISM-TREEVIEW  */
pre.language-treeview {
  padding-bottom: 0.75em;
}

.uk-table th {
  font-size: .675rem;
  color: #444;
}

.uk-table-small td,
.uk-table-small th {
  border: 1px solid #e5e5e5;
}

.uk-table tr > td:first-child {
  font-weight: bold;
}

/***
If you have page-specific styles, it is better to put them in a pages/ folder, 
in a file named after the page. For instance, it’s not uncommon to have very 
specific styles for the home page hence the need for a _homepage.scss file 
in pages/.
***/
/***
On large sites and applications, it is not unusual to have different themes. 
There are certainly different ways of dealing with themes but I personally 
like having them all in a themes/ folder.
***/
/***
The utilities/ folder contains various files related to general, reusable,
single-operation classes.

e.g: .text-uppercase { text-transform: uppercase; }
e.g: <p class="text-uppercase">foo</p>

Note that it is acceptable to disable Stylelint's `no-important` rule for utils.
***/
.no-scroll {
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}
